.header{
  background-color: #282E33;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo-holder{
      width: 200px;

      @media only screen and (max-width: 576px) {
        width: 100px;
      }
    }

    .menu {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;

      @media only screen and (max-width: 576px) {
        display: none;
      }

      .menu-links {
        text-decoration: none;
        display: block;
        padding: 20px 20px;
        color: #fff;

        &:hover {
          background-color: #777;
        }

        &:active {
          background-color: #1966B3;
        }
      }
    }


    .search{
      outline: none;
      height: 40px;

      & + label{
        background-color: #1966B3;
        background-size: cover;
        fill: #fff;
      }
    }

    .burger-menu{
      display: none;

      .hamburger-button{
      display: none;

      &__item{
        width: 24px;
        height: 1px;
        background: #2D314D;
        margin-bottom: 4px;
        display: none;
        @media only screen and (max-width: 576px) {
          display: block;
        }
      }
      &.active {
        .hamburger-button__item:first-child {
          transform: rotate(-45deg) translate(2px, 0);
          transform-origin: right top;
        }

        .hamburger-button__item:nth-child(2) {
          visibility: hidden;
        }

        .hamburger-button__item:last-child {
          transform: rotate(45deg) translate(-2px, -14px);
          transform-origin: left top;
        }
      }
    }
    }
  }
}